import renderBaseStructure from './modules/renderBaseStructure';
import createField from './modules/createField';
import renderField from './modules/renderField';
import movesControl from './modules/movesControl';
import sizeBtns from './modules/sizeBtns';
import startGameBtn from './modules/startGameBtn';
import { sound } from './modules/sound';
import { save } from './modules/save';
import gameBloker from './modules/gameBloker';
import { resultsBtn } from './modules/results';

document.addEventListener('DOMContentLoaded', (event) => {
    const savedArray = localStorage.getItem('array') ? JSON.parse(localStorage.getItem('array')) : null;
    let array = savedArray || createField();
    
    renderBaseStructure();

    gameBloker(true);

    renderField(array);

    movesControl(array);

    sizeBtns();

    startGameBtn();

    sound();

    resultsBtn();

    save();
});